    <li class="nav-item"><a class="nav-link" href="#contact">Contact</a></li>
    {#if replay}
      <ReplayLoader
        button="secondary"
        on:replayLoaded={(e) => {
          loading = false;
          replay = e.detail;
          replayStart = null;
          replayEnd = null;
          console.log(e);
        }}
        on:replayLoading={() => {
          replay = null;
          error = false;
          loading = true;
        }}
        on:replayError={() => {
          loading = false;
          error = true;
          replay = null;
        }}
      />
    {/if}
  </ul>
</nav>

